import { Component, Input, SimpleChanges, OnChanges, OnInit } from '@angular/core';
import { FormGroup, AbstractControl } from '@angular/forms';
import { IField } from '../../models/schema';
import { FormState } from '../../models/form-state';
import { FormStateService } from '../../services/form-state.service';

@Component({
  selector: 'de-re-crud-form-host',
  template: `<ng-container *ngFor="let field of fields">
  <ng-container [ngSwitch]="field.type">
    <de-re-crud-stamp-field-host
      *ngSwitchCase="'stamp'"
      [formId]="formId"
      [form]="form"
      [field]="field"
      [struct]="struct"
      [block]="block">
    </de-re-crud-stamp-field-host>
    <de-re-crud-input-field-host
      *ngSwitchDefault
      [formId]="formId"
      [form]="form"
      [parentPath]="parentPath"
      [parentForm]="parentForm"
      [field]="field"
      [struct]="struct"
      [block]="block">
    </de-re-crud-input-field-host>
  </ng-container>
</ng-container>
`
})
export class FormHostComponent implements OnInit, OnChanges {
  private _struct: string;
  private _block: string;

  @Input() formId: number;
  @Input() form: FormGroup;
  @Input() fields: IField[];
  @Input() parentForm: AbstractControl;
  @Input() parentPath: string;
  state: FormState;

  constructor(
    private stateService: FormStateService
  ) {}

  get struct() {
    return this._struct || this.state.options.struct;
  }

  @Input()
  set struct(value: string) {
    this._struct = value;
  }

  get block() {
    return this._block || this.state.options.block;
  }

  @Input()
  set block(value: string) {
    this._block = value;
  }

  ngOnInit() {
    this.state = this.stateService.get(this.formId);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.formId && !changes.formId.isFirstChange()) {
      this.ngOnInit();
      return;
    }
  }
}
